  color: white !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.primary_color{
  color: white;
}

.main_portfolio {
    position: relative;
    width: 100%;
  }
  
  .image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .middle a{
    text-decoration: none;
  }
  
  .main_portfolio:hover .image {
    opacity: 0.3;
  }
  
  .main_portfolio:hover .middle {
    opacity: 1;
  }
  
  .text {
    background-color: #04AA6D;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
  }
  .img-hover-zoom {
    position: relative;
    margin: auto;
    overflow: hidden;
    }
   
    /* [2] Transition property for smooth transformation of images */
    .img-hover-zoom img {
      max-width: 100%;
      transition: all 0.3s;
      display: block;
      width: 100%;
      height: auto;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
   
    /* [3] Finally, transforming the image when container gets hovered */
    .img-hover-zoom:hover img {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
  

label {
  font-size: 1.5rem;
  padding: 0.5rem 0;
}
input,
textarea {
  padding: 0.5rem 0.5rem;
  border-radius: 10px;
  outline: 0;
}
input[type="submit"] {
  margin: 2rem 0;
  font-size: 1.2rem;
  background-color: #2c3a47;
  border: 2px solid white;
  color: white;
  /* "to left" / "to right" - affects initial color */
  background: linear-gradient(to left, #2c3a47 50%, #04AA6D 50%) right;
  background-size: 200%;
  transition: 0.5s ease-out;
}

input[type="submit"]:hover {
  background-position: left;
}
textarea {
  height: 20vh;
}

