.ripple {
  margin: auto;
  margin-top: -5rem;
  background-color: white;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  position: relative;
  -webkit-animation: ripple 2s linear infinite;
          animation: ripple 2s linear infinite;
}
.ripple::before,
.ripple::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  -webkit-animation: inherit;
          animation: inherit;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.ripple::after {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

